$color_blue: #1EACC7;
$color_red: #ff5656;
* {
    font-family: 'Roboto', sans-serif; }

header {
    position: relative;
    height: 50px; }

nav {
    position: fixed;
    width: 100%;
    height: 50px;
    background: #212121;
    z-index: 2; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none; }

.menu_link {
    font-weight: 300;
    font-size: 15px;
    line-height: 15px;
    color: #FFFFFF; }

.menu_item {
    border-right: 2px dotted black;
    padding-right: 42px; }

.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 87px;
    background: rgba(0, 0, 0, 0.6); }

.subheader_logo {
    min-width: 100%;
    display: block;
    margin-top: 16px; }

.subheader_official {
    margin-top: 6px;
    opacity: 0.5;
    font-weight: 400;
    font-size: 13px;
    color: #FFFFFF; }

.subheader_call {
    margin-top: 18px;
    font-weight: 300;
    font-size: 12px;

    /* line-height: 14px; */
    text-align: right;
    color: #FFFFFF; }

.subheader_phone {
    display: block;
    margin-top: 3px;
    font-size: 22px;
    line-height: 22px;
    text-align: right;
    text-transform: uppercase;
    color: #FFFFFF; }

.subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }

.promo {
    min-height: 799px;
    background: url('../img/bg/uber_bg.png') center (center / cover) no-repeat;
    padding: 152px 0 186px 0; }

.promo_header {
    text-align: center;
    font-weight: 900;
    font-size: 60px;
    line-height: 60px;
    text-transform: uppercase;
    color: #FFFFFF; }
z {}
.promo_subheader {
    text-align: center;
    font-size: 36px;
    line-height: 36px;
    font-weight: 700;
    margin: 0;
    margin-top: 22px;
    text-transform: uppercase;
    color: #FFFFFF; }

.promo_descr {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    text-align: center;
    color: #FFFFFF;
    margin-top: 16px; }

.promo_btn {
    display: block;
    width: 259px;
    height: 63px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    margin: 118px auto 0 auto;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); }

.reasons {
    min-height: 849px;
    padding: 20px 0 67px 0; }

.label {
    margin: 0 auto;
    width: 115px;
    height: 27px;
    padding: 5px 12px;
    background: #000000;
    font-weight: 300;
    font-size: 11px;
    line-height: 20px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF; }

.title {
    margin-bottom: 0;
    font-weight: 700;
    font-size: 38px;
    text-align: center;
    text-transform: uppercase;
    color: #222222;
    margin-top: 35px; }

.reasons .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px; }

.reasons_round {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 116px;
    height: 116px;
    background: #1EACC7;
    border-radius: 100%;
    margin-right: 26px; }

.reasons_descr {
    width: 341px;

    .reasons_text {
        margin-top: 3px;
        font-weight: 400;
        font-size: 17px;
        line-height: 21px;
        color: #202020; }

    .reasons_subtitle {
        font-weight: 300;
        font-size: 17px;
        line-height: 21px;
        color: #1EACC7; } }

.mobile {
    min-height: 864px;
    padding: 46px 0 80px 0;
    background: url('../img/bg/mobile_bg.png') center (center / cover) no-repeat;
    &_item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        text-align: center;
        margin-top: 32px;
        padding: 91px 0 35px 0;
        width: 320px;
        height: 280px;
        opacity: 0.9;
        font-size: 24px;
        line-height: 31px;
        font-weight: 400;
        text-transform: uppercase;
        color: #ffffff; }
    &_item_1 {
        background: url('../img/bg/cards/1_card.png') center (center / cover) no-repeat; }
    &_item_2 {
        background: url('../img/bg/cards/2_card.png') center center/cover no-repeat; }
    &_item_3 {
        background: url('../img/bg/cards/3_card.png') center center/cover no-repeat; }
    &_item_4 {
        background: url('../img/bg/cards/4_card.png') center center/cover no-repeat; }
    &_item_5 {
        background: url('../img/bg/cards/5_card.png') center center/cover no-repeat; }
    &_item_6 {
        background: url('../img/bg/cards/6_card.png') center center/cover no-repeat; }
    &_item_plus {
        // display: flex
        justify-content: center;
        align-items: center;
        width: 26px;
        height: 26px;
        background-color: #000000;
        border-radius: 100%;
        // left: 50%
        transform: translate(50%);
        margin: 0 auto; }
    &_item_subtitle {
        margin: 0 auto; } }

.subtitle {
    margin-top: 19px;
    font-weight: 400;
    font-size: 17px;
    text-align: center; }
